import {
  Button,
  Card,
  Layout,
  Modal,
  Text, useTheme,
  withStyles,
} from '@ui-kitten/components';
import { useEffect, useRef, useState } from 'react';
import {StyleSheet, ScrollView, TouchableOpacity, Platform} from 'react-native';

import { useAppSelector } from '../../../app/hooks';
import { useIsAllowedToReviewQuery } from '../../api/classPagesApi';
import { selectDarkMode, selectUserKey } from '../../../app/appDataSlice';
import ExportModalClass from "./exportModalClass";
import {LinearGradient} from "expo-linear-gradient";
import {base_URL} from "../../../constants/admin";
import {useGetUserInfoQuery} from "../../api/usersApi";
import ModalContainer from "../../../components/ModalContainer";
import {locations, removeNumbersAndSpaces} from "../../../constants/map";
import openMap from 'react-native-open-maps'

function SingleClassPage({ eva, navigation, selectedClass = null }) {
  const [showModal, setShowModal] = useState(false);
  const scrollViewRef = useRef(null);
  const theme = useTheme();
  const userKey = useAppSelector(selectUserKey);
  const {data: userData} = useGetUserInfoQuery(userKey, {
    skip: !userKey,
  });
  const darkMode = useAppSelector(selectDarkMode);
  const [ professorReviewsObject, setProfessorReviewsObject ] = useState({})
  const filters = useAppSelector((state) => state.appData.classPages.filters);
  const isHMC = useAppSelector((state) => state.appData.classPages.isHMC);
  if (!selectedClass) {
    selectedClass = useAppSelector(
        (state) => state.appData.classPages.selectedClass
    );
  }
  const daysOfWeek = [
    'Monday',
    'Tuesday',
    'Wednesday',
    'Thursday',
    'Friday',
    'Saturday',
    'Sunday'
  ]


  const backgroundColor = darkMode ? eva.style.dark : eva.style.light;

  const { data: isAllowedToReviewRes } = useIsAllowedToReviewQuery({
    userKey,
    masterCourseCode: selectedClass.masterCourseCode,
  });

  useEffect(() => {
    if (scrollViewRef?.current) {
      scrollViewRef.current.scrollTo({
        y: 0,
        animated: false,
      });
    }
    if (selectedClass) {
      for (const time of selectedClass.times) {
        getReviewsFromProfessor(time)
      }
    }
  }, [selectedClass]);

  async function getReviewsFromProfessor(time) {
    const name = time.professorName
    let lastName
    try {
      lastName = time.professorName.split(' ')[1]
    } catch (e) {
      console.log('no last name')
      lastName = time.professorName
    }
    let res = await fetch(base_URL + '/classes/reviews/getReviewsByProfessor' + '?professorName=' + lastName + '&collegeName=' + userData.collegeName, {
      method: 'GET',
      headers: {
        'Content-Type': 'application/json',
        userKey: userKey,
      },
    })
    if (res.ok) {
      const reviews = await res.json()
      if (reviews.length > 0) {
        let newObject = {
          ...professorReviewsObject
        }
        newObject[name] = reviews
        setProfessorReviewsObject(newObject)
      }
    }
  }

  function buildTimeString(time) {
    // Compatibility with new course data layout for multiple times per day
    if (time.courseSchedule && time.courseSchedule.length > 0) {
      time = time.courseSchedule[0]
    }

    if (!time.startTime || time.startTime === '' || time.startTime === null) {
      return 'closed';
    }
    return (
      convertTo12HourTime(time.startTime) +
      ' - ' +
      convertTo12HourTime(time.endTime) +
      '\n'
    );
  }

  function convertTo12HourTime(time) {
    if (time.length !== 5) {
      time = '0' + time;
    }
    let hours = time.slice(0, 2);
    let minutes = time.slice(3, 5);
    let ampm = Number.parseInt(hours) > 12 ? 'pm' : 'am';
    hours = hours % 12;
    if (hours === 0) {
      hours = 12;
      ampm = 'pm';
    }
    return hours + ':' + minutes + ampm;
  }

  function getProfessorReviewsList() {
    const final = []
    for (const professor of Object.keys(professorReviewsObject)) {
      const reviews = professorReviewsObject[professor]
      final.push(
        <>
          <Text style={styles.subHeader}>All Reviews: {professor}</Text>
          {reviews.length === 0 ? (
              <Text style={{ alignSelf: 'center', textAlign: 'center', marginBottom: 10 }}>
                Something went wrong...
              </Text>
          ) : (
              reviews.map((review, index) => (
                  <Card key={index} disabled={true}>
                    <Text>{review.text + '\n\nPosted: ' + review.date}</Text>
                  </Card>
              ))
          )}
        </>
      )
    }
    return final
  }

  const averageGradeMSGModal = (
    <Modal
      visible={showModal}
      backdropStyle={styles.backdrop}
      style={{ width: '75%' }}
      onBackdropPress={() => setShowModal(false)}>
      <Card disabled={true}>
        <Text style={{ fontSize: 15, paddingTop: 5, paddingBottom: 10 }}>
          The Athena Network collects and aggregates the data anonymously
          submitted by the Course Review process and calculates what the average
          student's experience in this course will look like. Given that it is
          based on voluntarily submitted data, this information is to be used as
          a reference only. Please consult your advisor before making any
          impactful decisions.
        </Text>
        <Button onPress={() => setShowModal(false)}>Dismiss</Button>
      </Card>
    </Modal>
  );

  if (!selectedClass.times) {
    return null;
  }

  const [ schedulePopup, setSchedulePopup ] = useState(false)
  const [ allTheChildren, setAllTheChildren ] = useState([])

  function toggleSchedulePopUp(time: any) {
    let finalDays = {}
    for (const realTime of time.courseSchedule) {
      for (const day of daysOfWeek) {
        if (realTime.daysOfTheWeek.includes(day)) {
          const addition = convertTo12HourTime(realTime.startTime) + ' - '
              + convertTo12HourTime(realTime.endTime)
          if (finalDays[day]?.length > 0) {
            finalDays[day].push(addition)
          } else {
            finalDays[day] = [addition]
          }
        }
      }
    }

    setAllTheChildren(generateTable(finalDays))
    setSchedulePopup(true)
  }

  function generateTable(daysObject: any): any {
    const daysActive = Object.keys(daysObject)
    const stringArrays = Object.values(daysObject)
    return (
        <Layout style={{flexDirection: 'row'}}>
          <Layout>
            {
              daysActive.map((value) => {
                return (
                    <Layout>
                      <Text style={{height: 75, borderWidth: 1, borderColor: 'black', padding: 10}}>
                        {value}
                      </Text>
                    </Layout>
                )
              })
            }
          </Layout>
          <Layout>
            {
              stringArrays.map((value: string[]) => {
                return (
                    <Layout>
                      <Text style={{height: 75, borderWidth: 1, borderColor: 'black', padding: 10}}>
                        {value.join('\n')}
                      </Text>
                    </Layout>
                )
              })
            }
          </Layout>
        </Layout>
    )
  }

  return (
      <LinearGradient
          colors={
            darkMode
                ? [theme['color-basic-1100'], theme['color-basic-1100']]
                : [theme['color-primary-500'], theme['color-success-500']]
          }
          style={{ width: '100%', margin: 0, alignItems: 'center', flex: 1 }}
          start={{ x: 0, y: 0 }}
          end={{ x: 1, y: 0.4 }}>
    <ScrollView
      contentContainerStyle={{ alignSelf: 'center', maxWidth: 700 }}
      style={backgroundColor}
      showsVerticalScrollIndicator={false}
      ref={scrollViewRef}>
      <Layout style={[{ alignItems: 'center' }, backgroundColor]}>
        <ExportModalClass classData={selectedClass}/>
        <Text style={styles.title}>{selectedClass.courseName}</Text>
        <Text style={{ paddingTop: 30 }}>{selectedClass.masterCourseCode}</Text>

        <Text style={styles.subtitle}>
          {`${selectedClass.courseTerm} \u2022 credits: ${
            isHMC
              ? selectedClass.creditWeightHM
              : selectedClass.creditWeight
          }`}
        </Text>
        {selectedClass.timeSpentOutOfClass && (
          <Layout
            style={[
              {
                flexDirection: 'row',
                alignItems: 'flex-start',
                paddingRight: 5,
              },
              backgroundColor,
            ]}>
            <Text style={styles.subtitle}>Time Spent Per Week</Text>
            <Text style={{ fontSize: 12, lineHeight: 13 }}>i</Text>
            <Text style={styles.subtitle}>
              : {selectedClass.timeSpentOutOfClass} hours
            </Text>
          </Layout>
        )}
        {selectedClass.averageGrade && (
          <Layout
            style={[
              {
                flexDirection: 'row',
                alignItems: 'flex-start',
                paddingRight: 5,
              },
              backgroundColor,
            ]}>
            <Text style={styles.subtitle}>Average Grade</Text>
            <Text style={{ fontSize: 12, lineHeight: 13 }}>i</Text>
            <Text style={styles.subtitle}>: {selectedClass.averageGrade}</Text>
          </Layout>
        )}
        {selectedClass.difficulty && (
          <Layout
            style={[
              {
                flexDirection: 'row',
                alignItems: 'flex-start',
                paddingRight: 5,
              },
              backgroundColor,
            ]}>
            <Text style={styles.subtitle}>Estimated Difficulty</Text>
            <Text style={{ fontSize: 12, lineHeight: 13 }}>i</Text>
            <Text style={styles.subtitle}>
              : {selectedClass.difficulty} / 5
            </Text>
          </Layout>
        )}
        {selectedClass.rating && (
          <Layout
            style={[
              {
                flexDirection: 'row',
                alignItems: 'flex-start',
                paddingRight: 5,
              },
              backgroundColor,
            ]}>
            <Text style={styles.subtitle}>Average Rating</Text>
            <Text style={{ fontSize: 12, lineHeight: 13 }}>i</Text>
            <Text style={styles.subtitle}>: {selectedClass.rating} / 5</Text>
          </Layout>
        )}
      </Layout>
      <Layout style={backgroundColor}>
        <Text style={styles.courseDescription}>{selectedClass.summary}</Text>
      </Layout>
      <Layout style={{ ...backgroundColor, ...styles.boxed }}>
        <Text style={{ fontSize: 15, fontWeight: 'bold' }}>
          Stated Prerequisites:
        </Text>
        <Text style={{ fontSize: 15 }}>
          {selectedClass.prerequisites !== ''
            ? selectedClass.prerequisites
            : 'none'}
        </Text>

        <Text style={{ fontSize: 15, fontWeight: 'bold' }}>
          Stated Corequisites:
        </Text>
        <Text style={{ fontSize: 15 }}>
          {selectedClass.corequisites !== ''
            ? selectedClass.corequisites
            : 'none'}
        </Text>
      </Layout>
      <Layout style={[{ alignItems: 'flex-end' }, backgroundColor]}>
        {(selectedClass.timeSpentOutOfClass ||
          selectedClass.averageGrade ||
          selectedClass.difficulty ||
          selectedClass.rating) && (
          <TouchableOpacity onPress={() => setShowModal(!showModal)}>
            <Layout
              style={[
                {
                  flexDirection: 'row',
                  alignItems: 'flex-start',
                  paddingRight: 5,
                },
                backgroundColor,
              ]}>
              <Text style={{ fontSize: 12, lineHeight: 13 }}>i</Text>
              <Text style={styles.disclaimer}>How Do We Estimate This?</Text>
            </Layout>
          </TouchableOpacity>
        )}
      </Layout>
      <Layout style={backgroundColor}>
        {selectedClass.times.length > 0 && (
          <Layout style={backgroundColor}>
            <Text style={styles.subHeader}>Sections</Text>
            <Layout style={[styles.section, backgroundColor]}>
              <Text style={styles.headerText}>Course Code</Text>
              <Text style={styles.headerText}>SA/ST/PERM</Text>
              <Text style={styles.headerText}>Professor</Text>
              <Text style={styles.headerText}>Time</Text>
              <Text style={styles.headerText}>Location</Text>
            </Layout>
          </Layout>
        )}
        {selectedClass.times.map((time) => {
          return (
            <Layout
              key={time.courseCode}
              style={{ backgroundColor, ...styles.section }}>
              <Text style={styles.sectionText}>{time.courseCode}</Text>
              <Text style={styles.sectionText}>
                {time.seatsFilled} / {time.seatsFilled + time.seatsAvailable} / {time.permCount ? time.permCount : 0}
              </Text>
              <Text style={styles.sectionText}>{time.professorName}</Text>
              <Layout style={styles.sectionText}>
                {
                  time?.courseSchedule?.length > 1 ?
                      <TouchableOpacity onPress={() => toggleSchedulePopUp(time)} disabled={!(time?.courseSchedule?.length > 1)}>
                        <Text style={{...styles.sectionText, color: 'rgba(55,81,211,0.84)', textDecorationLine: 'underline'}}>
                          {Platform.OS === 'web' ? 'Click' : 'Press'} for{'\n'}schedule
                        </Text>
                      </TouchableOpacity> :
                      <Text style={styles.sectionText}>
                        {buildTimeString(time)}
                        {time?.courseSchedule?.length > 0 ? time.courseSchedule[0].daysOfTheWeek.join(', ') : time.daysOfTheWeek.join(', ')}
                      </Text>
                }
              </Layout>
              <TouchableOpacity disabled={!locations[removeNumbersAndSpaces(time.courseSchedule[0].classLocation)]} style={{flex: 1}} onPress={() => {
                openMap({
                  waypoints: [],
                  provider: "google",
                  query: locations[removeNumbersAndSpaces(time.courseSchedule[0].classLocation)].map(number => number.toString()).join(', '),
                  zoom: 18
                });
              }}>
                <Text style={locations[removeNumbersAndSpaces(time.courseSchedule[0].classLocation)] ? {...styles.sectionText, color: 'rgba(55,81,211,0.84)', textDecorationLine: 'underline'} : {flex: 1}}>
                  {time.courseSchedule[0].classLocation ? time.courseSchedule[0].classLocation : 'TBD'}
                </Text>
              </TouchableOpacity>
            </Layout>
          );
        })}
        {averageGradeMSGModal}
        <ModalContainer children={allTheChildren} backDropVisible={schedulePopup} setBackDropVisible={setSchedulePopup} title={'Course Time Schedule'}/>
      </Layout>
      {isAllowedToReviewRes?.isAllowed ? (
        <Button
          style={{ margin: 10 }}
          onPress={() => {
            navigation.navigate('PostReview');
          }}>
          Post Review
        </Button>
      ) : null}
      <Text style={styles.subHeader}>Class Reviews</Text>
      {selectedClass.reviews.length === 0 ? (
        <Text style={{ alignSelf: 'center', textAlign: 'center', marginBottom: 10 }}>
          No Class Reviews yet
        </Text>
      ) : (
        selectedClass.reviews.map((review, index) => (
          <Card key={index} disabled={true}>
            <Text>{review.text + '\n\nPosted: ' + review.date}</Text>
            {review.professor && (
              <Text>{'Taught By: ' + review.professor}</Text>
            )}
          </Card>
        ))
      )}
      {
        Object.keys(professorReviewsObject).length !== 0 ? getProfessorReviewsList() : null
      }
    </ScrollView>
      </LinearGradient>
  );
}

export default withStyles(SingleClassPage, (theme) => ({
  dark: {
    backgroundColor: theme['color-basic-900'],
  },
  light: {
    backgroundColor: theme['color-basic-200'],
  },
}));

